export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		};
	},
	onShow() {
		// 使用挂载方法获取用户数据
		let userInfo = this.getGlobalUser("globalUser");
		if (userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo;
		} else {
			this.userIsLogin = false;
			this.userInfo = {};
		}
	}
}
